import React from 'react';
import {Flex} from 'antd';
import MoviePagination from './movie-pagination/MoviePagination.jsx';
import MovieCard from "./movie-card/MovieCard.jsx";
import MovieMenu from "./movie-menu/MovieMenu.jsx";
import MovieSearchBar from "./movie-search-bar/MovieSearchBar.jsx";


/* const imgStyle = {
    display: 'block',
    width: 273,
}; */

function App() {
  const isDesktop = () => window.innerWidth >= 1440
  const movieBoxGap = isDesktop() ? 20 : 10;
  const movieCardWidth = isDesktop() ? 450 : 388;


  const calcBoxWidth = () => {
    const countMovieBox = Math.floor(window.innerWidth / movieCardWidth);
    const totalWidthOfMovieBoxGaps = (countMovieBox - 1) * movieBoxGap;
    return Math.floor((movieCardWidth * countMovieBox) + totalWidthOfMovieBoxGaps);
  }

  const boxStyle = {
    width: calcBoxWidth(),
    minWidth: movieCardWidth,
    height: '100%',
    borderRadius: 6,
    border: '1px solid #40a9ff'
  };

  return (
    <div className="App">
      <Flex style={{minWidth:movieCardWidth}} gap="middle" align="center" vertical>
        <MovieMenu />
        <MovieSearchBar calcBoxWidth={calcBoxWidth} />
        <Flex style={boxStyle} gap={movieBoxGap} wrap="wrap" align="flex-start" justify="flex-start">
          <MovieCard cardWidth={movieCardWidth} isDesktop={isDesktop}/>
        </Flex>
        <MoviePagination minWidth={movieCardWidth}/>
      </Flex>
    </div>
  );
}

export default App;
